/* General container */
.home-container {
    background-color: #201f1f;
    color: #f1f1f1;
    font-family: sans-serif;
    min-height: 100vh;
  }

/* Hero Section */
.hero-section {
position: relative;
width: 100%;
height: 100vh;
display: flex;
flex-direction: column;
justify-content: center;
align-items: flex-end; /* Changed from center to right-align content */
text-align: right; /* Align text to the right */
padding: 3%;
overflow: hidden;
}

.hero-background {
position: absolute;
top: 0;
left: 0;
width: 100vw;
height: 100vh;
object-fit: cover;
z-index: 0;
}

.hero-content {
display: flex;
flex-direction: column;
align-items: flex-start; /* Align children to the left */
text-align: left;
z-index: 2;
max-width: 40rem;
}

.hero-title {
font-size: 4rem;
font-weight: bold;
margin-bottom: 1rem;
line-height: 1.2;
z-index: 2;
}

.hero-subtitle {
font-size: 1.125rem;
max-width: 40rem;
margin-bottom: 2rem;
z-index: 2;
}

.hero-button {
color: #f1f1f1;
background-color: rgb(0, 0, 0);
padding: 0.75rem 1.5rem;
border-radius: 99px;
font-size: 1.125rem;
cursor: pointer;
transition: opacity 0.2s ease-in-out;
z-index: 2;
}

.hero-button:hover {
opacity: 0.8;
}


.about-title {
font-size: 2rem;
font-weight: 600;
margin-bottom: 3rem;
}



.hero-video {
width: 35vw;
height: 35vh;
margin-bottom: 2rem;
z-index: 2;
}

.about-section {
    background-color: #212121;
    padding: 5rem 1.5rem;
    text-align: center;
    }

    .about-title {
    font-size: 3.5rem;
    font-weight: 600;
    margin-bottom: 4rem;
    }

    .about-item {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
    margin-bottom: 4rem;
    flex-direction: row;
    text-align: left;
    padding: 0 2rem;
    }

    .about-item.reverse {
    flex-direction: row-reverse;
    }

    .about-img {
    background-color: rgb(0, 0, 0);
    width: 30vw;
    border-radius: 40px;
    object-fit: cover;
    height: 40vh;
    }

    .about-text {
    max-width: 40%;
    }

    .about-item-title {
    font-size: 2.8rem;
    font-weight: 600;
    margin-bottom: 1rem;
    }

    .about-item-text {
    font-size: 1.4rem;
    line-height: 1.6;
    }

    @media (max-width: 768px) {
    .about-item,
    .about-item.reverse {
        flex-direction: column;
        text-align: center;
    }

    .about-img,
    .about-text {
        max-width: 100%;
        width: 100%;
    }

    .about-img {
        height: 200px;
    }
    }


/* CTA Section */
.cta-section {
background-color: #212121;
padding: 4rem 1.5rem;
text-align: center;
}

.cta-title {
font-size: 2rem;
font-weight: 600;
margin-bottom: 1rem;
}


.cta-button {
color: #f1f1f1;
background-color: black;
padding: 0.75rem 2rem;
border-radius: 9999px;
font-size: 1.125rem;
cursor: pointer;
transition: opacity 0.2s ease-in-out;
}

.cta-button:hover {
opacity: 0.8;
}



/* Responsive Hero Section */
@media (max-width: 1024px) {
.hero-section {
    align-items: center; /* Center content horizontally */
    justify-content: flex-start;
    text-align: center;
    height: 60vh;
    }
.hero-content {
    max-width: 100vw;
    align-items: center;
    text-align: center;
    padding: 1rem;
}

.hero-title {
    font-size: 5rem; /* Increased from 3rem */
    margin-bottom: 1.5rem;
}

.hero-subtitle {
    font-size: 1.25rem; /* Increased from 1rem */
    margin-bottom: 2rem;
}

.hero-video {
    width: 60vw;
    height: 30vh; /* Increased from 80vh */
    margin-bottom: 0;  /* Remove bottom margin to let it fill */
    object-fit: cover;
}

.hero-button {
    margin-top: 10vh;
    font-size: 2.25rem; /* Increased from 1rem */
    padding: 1rem 2rem;  /* Increased padding */
}
.hero-background {
    object-position: right;
    }
}

@media (max-width: 900px) {
.hero-section {
    align-items: center; /* Center content horizontally */
    justify-content: flex-start;
    text-align: center;
    height: 60vh;
    }

.hero-content {
    align-items: center !important;
    text-align: center !important;
    max-width: 100%;
}

.hero-title {
    font-size: 2.2rem;
}

.hero-subtitle {
    font-size: 0.95rem;
    max-width: 90%;
}
.hero-video {
    width: 90vw;
    height: 30vh; /* Increased from 80vh */
    margin-bottom: 0;  /* Remove bottom margin to let it fill */
    object-fit: cover;
    }

.hero-button {
    font-size: 1.95rem;
    padding: 0.5rem 1rem;
}
}
