/* Main container */
.login-container {
    background-color: #000000;
    color: #ffffff;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    padding: 2rem;
  }

/* Form wrapper */
.login-form {
  background-color: #0a0a0a;
  padding: 3rem 2.5rem;
  border-radius: 20px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
}

/* Heading */
.login-title {
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
  margin-bottom: 2rem;
  color: #f2f2f2;
}

/* Input fields */
.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.5rem;
}

.form-label {
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
  color: #cccccc;
}

.form-input {
  padding: 0.85rem 1rem;
  border-radius: 12px;
  border: 1px solid #333333;
  background-color: #121212;
  color: #ffffff;
  font-size: 1rem;
  outline: none;
  transition: border-color 0.2s ease-in-out;
}

.form-input:focus {
  border-color: #4dd0e1;
}

/* Forgot password */
.forgot-link {
  text-align: right;
  margin-top: -1rem;
  margin-bottom: 1.5rem;
}

.forgot-link a {
  color: #4dd0e1;
  font-size: 0.9rem;
  text-decoration: none;
}

.forgot-link a:hover {
  text-decoration: underline;
}

/* Submit button */
.submit-button {
  background-color: #ffffff;
  color: #000000;
  font-weight: 600;
  font-size: 1rem;
  padding: 0.75rem;
  border: none;
  border-radius: 9999px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #e5e5e5;
}

/* Google login button wrapper */
.google-login {
  margin-top: 1.5rem;
  text-align: center;
  border-radius: 9999px;
}

/* Responsive adjustments */
@media (max-width: 1080px) {
  .login-container {
      align-items: flex-start; /* Move content to top */
      padding-top: 3rem;       /* Add top spacing */
    }

    .login-form {
      padding: 1.5rem 1rem; /* Reduce padding for smaller screens */
      margin-top: 0;        /* Prevent unnecessary vertical shift */
    }

    .login-title {
      font-size: 1.75rem;
    }
  }